class Counter {
  constructor() {
    this.currentCount = 0;

    this.countElement = document.querySelector(".CounterNumber");

    //decrease counter by 1
    this.decreaseButton = document.querySelector(".button-decrease");
    this.decreaseButton.addEventListener("click", () => {
      this.decrease();
    });

    //reset counter to 0
    this.resetButton = document.querySelector(".button-reset");
    this.resetButton.addEventListener("click", () => {
      this.reset();
    });

    //increase counter by 1
    this.increaseButton = document.querySelector(".button-increase");
    this.increaseButton.addEventListener("click", () => {
      console.log(this);
      this.increase();
    });
  }

  updateCount() {
    this.countElement.textContent = this.currentCount;
    console.log("count");
  }
  increase() {
    this.currentCount++;
    this.updateCount();
  }
  decrease() {
    this.currentCount--;
    this.updateCount();
  }
  reset() {
    this.currentCount = 0;
    this.updateCount();
  }
}

const firstCount = new Counter();
